// rootReducer.ts

import {
  WS_CONNECTION_SUCCESS,
  WS_CONNECTION_ERROR,
  WS_CONNECTION_CLOSED,
  WS_GET_MESSAGE,
  TWSActions,
  TMessage
} from "../actions/ws-actions";

type TWSState = {
  wsConnected: boolean;
  messages: TMessage | null;
  error?: Event;
};

const initialState: TWSState = {
  wsConnected: false,
  messages: null,
};

//    WebSocket
export const wsReducer = (state = initialState, action: TWSActions):TWSState => {
  switch (action.type) {
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        wsConnected: true,
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        error: action.payload,
        wsConnected: false,
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false,
      };

    //      WS_GET_MESSAGE
    //  ,     
    //  messages  ,    
    case WS_GET_MESSAGE:
      return {
        ...state,
        error: undefined,
        messages: JSON.parse(action.payload),
      };
    default:
      return state;
  }
};
